@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}

.header {
  background-color: #301b96;
  height: 10%;
  width: 100%;
  margin: 0px 10px 10px 0px;
  padding: 0px 10%;
}
.header nav {
  height: 100%;
}
.header ul {
  height: 100%;
  list-style: none;
  color: white;
  display: flex;
  margin: 0;
  padding: 10px;
  align-items: center;
  justify-content: center;
}
.header a {
  color: white;
  text-decoration: none;
  font-size: 20px;
}
.header li {
  margin: 0 1rem;
  width: 5rem;
  list-style: none;
  display: flex;
}
.header a:hover,
.header a:active {
  color: #966d1b;
  padding-bottom: 0.45rem;
}
header a.active {
  color: #966d1b;
  padding-bottom: 0.45rem;
  text-decoration: underline;
}
ul {
  list-style: none;
  text-align: center;
}

